<template>
	<!--编辑地址-->
	<div class="bianjidizhi">
		<!--编辑地址-->
		<div class="bjdz">
			<div @click="backGo"><img src="/img/wode/shezhi/shezhi-back.png" alt="" /></div>
			<div>编辑地址</div>
			<div>保存</div>
		</div>
		<!--编辑地址项目-->
		<div class="bjdz-items">
			<ul>
				<li class="item">
					<div class="item-left">
						<span style="margin-right:20px" class="item-m">收件人</span>
						<input type="text" v-model="bianjiData.shr" />
					</div>
				</li>
				<li class="item">
					<div class="item-left">
						<span style="margin-right:20px" class="item-m">手机号</span>
						<input type="text" v-model="bianjiData.phone" />
					</div>
					<!--<div class="item-right"></div>-->
				</li>
				<li class="item">
					<div class="item-left-two">所在地区</div>
					<div class="item-right mydiqu" @click="xuanzedq">
						<!--<div v-if="bianjiData.diqu">
							<div class="diqu item-m" v-text="bianjiData.diqu"></div>
						</div>-->
						<!--<div v-else>-->
						<van-cell class="diqu item-m" v-model="diqu" :value="bianjiData.diqu"></van-cell>
						<!--</div>-->
						<img src="/img/wode/jichu/wode-back.png" alt="" />
					</div>
				</li>
				<li class="item">
					<div class="item-left-two">街道</div>
					<div class="item-right" @click="xuanzejd">
						<div class="jiedao item-m"></div>
						<img src="/img/wode/jichu/wode-back.png" alt="" />
					</div>
				</li>
				<li class="item">
					<input class="item-left xiangxidz" v-model="bianjiData.xxdz" />
				</li>
			</ul>
		</div>
		<!--遮罩层-->
		<div class="bj" v-show="myarea"></div>
		<!--选择地区-->
		<div class="myArea" v-show="myarea">
			<van-area :area-list="areaList" @confirm="onChange" @cancel="onClose" />
		</div>
	</div>
</template>

<script>
	export default {
		name: "bianjidizhi",
		data: function() {
			return {
				bianjiData: {},
				areaList: {
					province_list: {
						'110000': '北京市',
						'120000': '天津市'
					},
					city_list: {
						'110100': '北京市',
						'110200': '县',
						'120100': '天津市',
						'120200': '县'
					},
					county_list: {
						'110101': '东城区',
						'110102': '西城区',
						'110105': '朝阳区',
						'110106': '丰台区',
						'120101': '和平区',
						'120102': '河东区',
						'120103': '河西区',
						'120104': '南开区',
						'120105': '河北区',
						// ....
					}
				},
				myarea: false,
				diqu: ''
			}
		},
		mounted() {
			this.$axios.get('/json/shouhuodizhi.json').then(res => {
				//				console.log(res)
				//				console.log(this.$route.params.idData)
				for(var i in res.data) {
					if(res.data[i].id == this.$route.params.idData) {
						this.bianjiData = res.data[i]
					}
				}
			}).catch(err => {
				console.log(err)
			})
		},
		methods: {
			// 选择地区
			xuanzedq() {
				//弹出层
				this.myarea = !this.myarea
			},
			// 选择街道
			xuanzejd() {
				//弹出层
			},
			// 弹出层确认按钮事件
			onChange(picker, value, index) {
				console.log(value, index)
				var areaName = ''
				for(var i = 0; i < picker.length; i++) {
					areaName = areaName + picker[i].name + ' '
				}
				this.diqu = areaName
				this.myarea = !this.myarea
			},
			// 关闭弹出层
			onClose(){
				this.myarea = !this.myarea
			},
			backGo(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style scoped>
	.bianjidizhi {
		width: 100%;
		height: 100%;
		padding: 0 30px;
		position: relative;
	}
	/*收货地址*/
	
	.bianjidizhi .bjdz {
		width: 100%;
		padding: 80px 0 30px;
		font-size: 22px;
		color: #333333;
		background-color: white;
		font-weight: bold;
		display: flex;
		justify-content: space-between;
	}
	
	.bianjidizhi .bjdz-items {
		font-size: 20px;
		color: #333333;
		line-height: 40px;
	}
	
	.bianjidizhi .item {
		margin: 20px 0;
		display: flex;
		justify-content: space-between;
	}
	
	.bianjidizhi .item .item-m {
		margin-right: 20px;
	}
	
	.bianjidizhi .item .item-left {
		width: 80%;
		display: flex;
		justify-content: space-between;
	}
	
	.bianjidizhi .item .item-left-two {
		width: 50%;
		display: flex;
		justify-content: space-between;
	}
	
	.bianjidizhi .item .item-right div {
		display: inline-block;
	}
	.bianjidizhi .item .mydiqu{
		display: flex;
		justify-content: space-between;
	}
	.bianjidizhi .item .item-left input {
		width: 80%;
		border: none;
	}
	
	.bianjidizhi .item .xiangxidz {
		width: 100%;
		border: none;
	}
	/*遮罩层*/
	
	.bianjidizhi .bj {
		width: 100%;
		height: 100%;
		background-color: #808080;
		opacity: 0.4;
		position: absolute;
		left: 0;
		top: 0;
	}
	/*弹出层*/
	
	.bianjidizhi .myArea {
		width: 100%;
		position: fixed;
		left: 0;
		bottom: 0;
	}
	
	.bianjidizhi .diqu {
		/*position: relative;*/
		display: inline-block;
		/*box-sizing: border-box;*/
		/*width: 100%;*/
		padding: 0;
		overflow: hidden;
		color: #323233;
		font-size: 18px;
		line-height: 40px;
		/*background-color: #fff;*/
	}
</style>